import React, { useState } from "react";
import "./App.css";

interface IGraphItem {
  name?: string;
  count?: number;
}

const useForceUpdate = () =>{
  const [value, setValue] = useState(0); // integer state
  return () => setValue(value => value + 1); // update the state to force render
}

export default function Home() {
  const blankItems: IGraphItem[] = [];

  const forceUpdate = useForceUpdate();
  const [graphItems, setGraphItems] = useState(blankItems);
  const [graphTitle, setGraphTitle] = useState("");
  const [barColour, setBarColour] = useState("");
  const [textColour, setTextColour] = useState("");

  const renderBars = () => {
    let totalCount = 0;
    graphItems.forEach((graphItem) => {
      if (graphItem) {
        totalCount += graphItem.count || 0;
      }
    });

    return graphItems.map((graphItem, index) => {
      if (graphItem.name && graphItem.count) {
        const barWidth = ((graphItem.count || 0) / totalCount) * 100 + "%";
        return (
          <div key={`graph-bar-${index}`} className="graph-item">
            <div className="graph-info">
              {graphItem.name ? graphItem.name : ""} <span className="graph-count">{graphItem.count ? `(${graphItem.count})` : ""}</span>
            </div>
            <div className="graph-bar" style={{maxWidth : barWidth, backgroundColor: barColour}}>
              &emsp;
            </div>
          </div>
        )
      }

      return "";
    });
  };

  const renderForm = () => {
    return graphItems.map((graphItem, index) => {
      return (
        <div className="form-item">
          <input key={`graph-item-${index}-name`} data-index={index} onChange={updateName} placeholder="Item name" />
          <input key={`graph-item-${index}-count`} data-index={index} type="number" min="0" onChange={updateValue} placeholder="Item value" />
        </div>
      );
    });
  };

  const updateName = (event: React.ChangeEvent<HTMLInputElement>) => {
    const newValue = event.target.value;
    const currentItems = graphItems;
    const currentIndex = parseInt(event.target.getAttribute("data-index") as string);

    currentItems[currentIndex]["name"] = newValue;
    setGraphItems(currentItems);
  };

  const updateValue = (event: React.ChangeEvent<HTMLInputElement>,) => {
    const newValue = event.target.value;
    const currentItems = graphItems;
    const currentIndex = parseInt(event.target.getAttribute("data-index") as string);

    currentItems[currentIndex]["count"] = parseInt(newValue);
    console.log(currentItems);
    setGraphItems(currentItems);
  };

  const updateTitle = (event: React.ChangeEvent<HTMLInputElement>) => {
    const newValue = event.target.value;
    setGraphTitle(newValue);
  };

  const updateTextColour = (event: React.ChangeEvent<HTMLInputElement>) => {
    const newValue = event.target.value;
    setTextColour(newValue);
  };

  const updateBarColour = (event: React.ChangeEvent<HTMLInputElement>) => {
    const newValue = event.target.value;
    setBarColour(newValue);
  };

  const addNewItem = () => {
    setGraphItems([
      ...graphItems,
      {}
    ]);
  };

  return (
    <div className="chart" style={{color: textColour}}>
      <div className="main-chart">
        <div className="graph-title">
          {graphTitle}
        </div>
        {renderBars()}
      </div>
      <div className="chart-options">
        <div className="title-form">
          <input value={graphTitle} placeholder="Graph Title" onChange={(event) => updateTitle(event)} />
          <input value={barColour} placeholder="Bar Colour" onChange={(event) => updateBarColour(event)} />
          <input value={textColour} placeholder="Text Colour" onChange={(event) => updateTextColour(event)} />
        </div>
        {renderForm()}
        <button onClick={() => addNewItem()}>
          Add New Item
        </button>
        <button onClick={() => forceUpdate()}>
          Update Values
        </button>
      </div>
    </div>
  );
}
